
import React, { useEffect, useState } from 'react';
import { RiArrowRightLine, RiCloseCircleLine, RiSliceFill } from "react-icons/ri";
import { useWeb3React } from '@web3-react/core';
import { NotificationManager } from 'react-notifications';
import { useNavigate, Link } from 'react-router-dom';
import Layout from "../../component/layout/layout";
import { FaArrowDown } from "react-icons/fa";
import CustomLoader from "../../component/Spinner";
import defaultImage from "../../assets/images/unknown_user.webp";
import ExpandedComponent from "../../component/UserDetail";
import './admin.scss';
import { AiFillCopy } from "react-icons/ai";
import { copyToClipboard } from "../../util";

import DataTable, {
    TableColumn,
    createTheme,
} from "react-data-table-component";
import { getAllByDisplayValue } from '@testing-library/react';
import axios from "axios";
//axios.defaults.baseURL = "http://127.0.0.1"
createTheme(
    "solarized",
    {
        text: {
            primary: "var(--light)",
        },
        background: {
            default: "var(--secondary)",
        },
        button: {
            default: "var(--light)",
            hover: "rgba(0,0,0,.08)",
            focus: "rgba(255,255,255,.12)",
            disabled: "rgba(255, 255, 255, .34)",
        },
    },
    "dark"
);

const customStyles = {
    rows: {
        style: {
            padding: "5px",
            fontSize: "16px",
            fontWeight: 700,
        },
    },
    headCells: {
        style: {
            padding: "10px",
            color: "white",
            fontSize: "20px",
            fontWeight: "bold",
            minHeight: "40px",
        },
    },
    pagination: {
        style: {
            fontSize: "14px",
            fontWeight: 600,
        },
    },
};

const DepositUsdtTable = () => {

    const [loading, setLoading] = useState(false);
    const [data, setData] = useState([]);

    const columns = [
        {
            name: "Id",
            selector: (row) => row.id,
            sortable: true,
            width: "60px",
        },
        {
            name: "Address",
            selector: (row) => {
                return (
                    <div className='dis-f ai-c'>
                        <div className="mr-2">
                            <AiFillCopy onClick={() => copyToClipboard(row.useraddress)} style={{ color: 'lightgreen', cursor: 'pointer' }} />
                        </div>
                        <div className="">
                            {row.useraddress && row.useraddress.slice(0, 6) + '...' + row.useraddress.slice(-4)}
                        </div>
                    </div>
                )
            },
            sortable: true,
            width: "200px",
        },
        {
            name: "Asset",
            selector: (row) => row.assets,
            sortable: true,

        },
        {
            name: "Type",
            selector: (row) => row.type,
            sortable: true,

        },
        {
            name: "Date",
            selector: (row) => { return new Date(row.time).toLocaleDateString() + ' ' + new Date(row.time).toLocaleTimeString() },
            sortable: true,
            width: "300px",
        },
        {
            name: "Amount",
            selector: (row) => row.amount,
            sortable: true,
        },
        {
            name: "State",
            selector: (row) => {
                return row.status == 1 ? (
                    <select className='select-action' name="" id="" onChange={(e) => { updateState(e.target.value, row.id) }}>
                        <option value="1">Pending</option>
                        <option value="2">Accept</option>
                        <option value="3">Reject</option>
                        <option value="4">Finished</option>
                    </select>
                ) : row.status == 2 ? (
                    <select className='select-action' name="" id="" onChange={(e) => { updateState(e.target.value, row.id) }}>
                        <option value="2">Accept</option>
                        <option value="1">Pending</option>
                        <option value="3">Reject</option>
                        <option value="4">Finished</option>
                    </select>
                ) : row.status == 3 ? (
                    <select className='select-action' name="" id="" onChange={(e) => { updateState(e.target.value, row.id) }}>
                        <option value="3">Reject</option>
                        <option value="1">Pending</option>
                        <option value="2">Accept</option>
                        <option value="4">Finished</option>
                    </select>
                ) : (
                    <select className='select-action' name="" id="" onChange={(e) => { updateState(e.target.value, row.id) }}>
                        <option value="4">Finished</option>
                        <option value="1">Pending</option>
                        <option value="2">Accept</option>
                        <option value="3">Reject</option>
                    </select>
                )
            },
        },
    ];
    const updateState = async (type, id) => {
        try {
            axios.post('/updateDepositUsdtTransactionAdmin', { id: id, type: type }).then((e) => {
                if (e.data == "success") {
                    NotificationManager.success("success")
                }
            })
        } catch (error) {
            NotificationManager.error(error.message)
        }

    }
    const getAlldata = async () => {
        try {
            axios.post('/getDepositUsdtTransactionAdmin', {}).then((e) => {
                if (e.data) {
                    setData(e.data)
                }
            })
        } catch (error) {
            NotificationManager.error(error.message)
        }
    }
    useEffect(() => {
        getAlldata()
    }, []);

    return (
        <Layout>
            <h1 className='white'>Deposit USDT</h1>
            <br />
            <DataTable
                columns={columns}
                data={data}
                customStyles={customStyles}
                fixedHeader
                sortIcon={<FaArrowDown />}
                // expandableRows
                // expandableRowsComponent={ExpandedComponent}
                progressPending={loading}
                theme="solarized"
                pagination
                progressComponent={<CustomLoader />}
            />
        </Layout>
    );
};

export default DepositUsdtTable;
