import { useState, useEffect } from "react";
import { FaTelegramPlane, FaPlus, FaMinus } from "react-icons/fa";
import Layout from "../../component/layout/layout";
import { useWeb3React, UnsupportedChainIdError } from '@web3-react/core';
import { useNavigate } from 'react-router-dom';
import "./dashboard.scss";

import { NotificationContainer, NotificationManager } from 'react-notifications';
import { ethers } from 'ethers'
import axios from "axios";
import abi from "../../util/ABI.json"
import { injected, walletconnect, walletlink } from '../../util/connector';
import { AiOutlineClose, AiOutlineDown } from "react-icons/ai";
//axios.defaults.baseURL = "http://127.0.0.1"

const depositAddress = "0x7ddb60777Bd05e9cc129bD0Ea0AAA6A77cfB7BD4"
const usdcAddress = "0x2791Bca1f2de4661ED88A30C99A7a9449Aa84174"
const usdtAddress = "0xc2132D05D31c914a87C6611C10748AEb04B58e8F"

const mockHis = [
	{
		date: '25th March, 2023',
		content: 'USDC token launched on Arbitrage Plus for staking'
	},
	{
		date: '26th March, 2023',
		content: 'USDT token launched on Arbitrage Plus for staking'
	},
	{
		date: '27th March, 2023',
		content: 'Arbitrage Plus officially launches to the public'
	}
]

const ConfirmWithdrawUsdcModal = ({withdrawUsdcModal,setWithdrawUsdcModal,withdrawUsdc})=>{
	
	const [height, setHeight] = useState(0);
	useEffect(() => {
		setHeight(window.innerHeight);
	}, [window.innerHeight])

	return(
			<div className={`request-setting-modal-container ${withdrawUsdcModal ? 'modal-show' : ''}`}>
					<div
						className="request-setting-modal-bg"
						onClick={() => setWithdrawUsdcModal(!withdrawUsdcModal)}
					></div>
					<div className="request-setting-modal-body" style={{ maxHeight: `${height - 150}px` }}>
						<div className="request-modal-header w-full m-auto p-5 ">
							<div className="dis-f ai-c jc-sb w-full">
								<h4 className="white-color">Are you sure?</h4>
								<div onClick={() => setWithdrawUsdcModal(!withdrawUsdcModal)} className="p-2 rounded-full cu-po">
									<AiOutlineClose className="white-color" fontSize={'25px'} />
								</div>
							</div>
						</div>
						<div className="p-10 w-full modal-content">
							<div className="row">
								<div className="col-sm-6 col-md-6 mb-6">
									<button className='w-full' onClick={withdrawUsdc}>Yes</button>
								</div>
								<div className="col-sm-6 col-md-6 mb-6">
									<button className='w-full withdrow-btn' onClick={() => setWithdrawUsdcModal(!withdrawUsdcModal)}>No</button>
								</div>
							</div>
						</div>
					</div>
				</div>
	)
}
